import { supabase } from "./supabase";

/**
 * Servio para gerenciar a relao entre aulas e alunos utilizando a tabela de juno aula_alunos
 */
export const aulaAlunosService = {
  /**
   * Adiciona um aluno a uma aula
   * @param {string} aulaId - ID da aula
   * @param {string} alunoId - ID do aluno
   * @returns {Promise<Object>} - Os dados da relao criada
   */
  async adicionarAluno(aulaId, alunoId) {
    console.log(
      `[AulaAlunosService] Tentando adicionar aluno ${alunoId}  aula ${aulaId}`
    );

    try {
      // Verificar se os IDs so vlidos
      if (!aulaId || !alunoId) {
        console.error("[AulaAlunosService] IDs invlidos:", {
          aulaId,
          alunoId,
        });
        return {
          error: true,
          message: "IDs de aula ou aluno invlidos",
          details: { aulaId, alunoId },
        };
      }

      const { data, error } = await supabase
        .from("aula_alunos")
        .insert([{ aula_id: aulaId, aluno_id: alunoId }])
        .select()
        .single();

      if (error) {
        // Se o erro for de duplicao (aluno j na aula), retorna um objeto de erro especfico
        if (error.code === "23505") {
          // Cdigo para violao de restrio unique
          console.log(
            `[AulaAlunosService] Aluno ${alunoId} j existe na aula ${aulaId}`
          );
          return {
            error: true,
            message: "Este aluno j est na aula",
            isDuplicate: true,
          };
        }
        console.error(`[AulaAlunosService] Erro ao adicionar aluno:`, error);
        return {
          error: true,
          message: error.message || "Erro ao adicionar aluno  aula",
          details: error,
        };
      }

      console.log(
        `[AulaAlunosService] Aluno ${alunoId} adicionado com sucesso  aula ${aulaId}`
      );
      return data;
    } catch (err) {
      console.error(`[AulaAlunosService] Erro crtico:`, err);
      return {
        error: true,
        message: err.message || "Erro inesperado ao adicionar aluno",
        details: err,
      };
    }
  },

  /**
   * Remove um aluno de uma aula
   * @param {string} aulaId - ID da aula
   * @param {string} alunoId - ID do aluno
   * @returns {Promise<boolean>} - Verdadeiro se a remoo foi bem-sucedida
   */
  async removerAluno(aulaId, alunoId) {
    console.log(
      `[AulaAlunosService] Removendo aluno ${alunoId} da aula ${aulaId}`
    );

    try {
      // Validar parmetros
      if (!aulaId || !alunoId) {
        console.error("[AulaAlunosService] IDs invlidos:", {
          aulaId,
          alunoId,
        });
        return false;
      }

      // Verificar se a relao existe antes de tentar remover
      const { data: existingRelation, error: checkError } = await supabase
        .from("aula_alunos")
        .select("*")
        .eq("aula_id", aulaId)
        .eq("aluno_id", alunoId)
        .maybeSingle();

      if (checkError) {
        console.error(
          "[AulaAlunosService] Erro ao verificar relao existente:",
          checkError
        );
        throw checkError;
      }

      // Se no existe relao, no precisamos fazer nada
      if (!existingRelation) {
        console.log(
          `[AulaAlunosService] Aluno ${alunoId} no est na aula ${aulaId}`
        );
        return true;
      }

      // Remover a relao
      const { error } = await supabase
        .from("aula_alunos")
        .delete()
        .match({ aula_id: aulaId, aluno_id: alunoId });

      if (error) {
        console.error("[AulaAlunosService] Erro ao remover aluno:", error);
        throw error;
      }

      console.log(
        `[AulaAlunosService] Aluno ${alunoId} removido com sucesso da aula ${aulaId}`
      );
      return true;
    } catch (err) {
      console.error("[AulaAlunosService] Erro crtico ao remover aluno:", err);
      throw err;
    }
  },

  /**
   * Obtm todos os alunos de uma aula
   * @param {string} aulaId - ID da aula
   * @returns {Promise<Array>} - Lista de alunos com dados completos
   */
  async getAlunosDaAula(aulaId) {
    console.log(`[AulaAlunosService] Buscando alunos da aula ${aulaId}`);

    try {
      // Validar parmetro
      if (!aulaId) {
        console.error("[AulaAlunosService] ID de aula invlido");
        return [];
      }

      const { data, error } = await supabase
        .from("aula_alunos")
        .select("aluno_id")
        .eq("aula_id", aulaId);

      if (error) {
        console.error(
          `[AulaAlunosService] Erro ao buscar relaes de alunos:`,
          error
        );
        throw error;
      }

      if (!data || data.length === 0) {
        console.log(
          `[AulaAlunosService] Nenhum aluno encontrado para a aula ${aulaId}`
        );
        return [];
      }

      // Obter os dados completos dos alunos
      const alunoIds = data.map((item) => item.aluno_id);

      console.log(
        `[AulaAlunosService] Encontrados ${alunoIds.length} IDs de alunos, buscando detalhes...`
      );

      const { data: alunos, error: alunosError } = await supabase
        .from("alunos")
        .select("*")
        .in("id", alunoIds);

      if (alunosError) {
        console.error(
          `[AulaAlunosService] Erro ao buscar detalhes dos alunos:`,
          alunosError
        );
        throw alunosError;
      }

      console.log(
        `[AulaAlunosService] Recuperados ${
          alunos?.length || 0
        } alunos com dados completos`
      );
      return alunos || [];
    } catch (err) {
      console.error(
        `[AulaAlunosService] Erro crtico ao buscar alunos da aula:`,
        err
      );
      // Retornar array vazio em vez de propagar o erro para evitar quebrar a UI
      return [];
    }
  },

  /**
   * Atualiza a lista de alunos de uma aula
   * @param {string} aulaId - ID da aula
   * @param {Array<string>} alunoIds - Lista de IDs de alunos
   * @returns {Promise<boolean>} - Verdadeiro se a atualizao foi bem-sucedida
   */
  async atualizarAlunosDaAula(aulaId, alunoIds) {
    console.log(
      `[AulaAlunosService] Atualizando alunos da aula ${aulaId}`,
      alunoIds
    );

    try {
      // Validar entrada
      if (!aulaId) {
        console.error("[AulaAlunosService] ID da aula invlido");
        return false;
      }

      if (!Array.isArray(alunoIds)) {
        console.error(
          "[AulaAlunosService] Lista de IDs de alunos no  um array"
        );
        return false;
      }

      // Filtrar apenas IDs vlidos (no nulos, no undefined, no vazios)
      const alunoIdsValidos = alunoIds.filter(
        (id) => id && id.toString().trim() !== ""
      );

      console.log(
        `[AulaAlunosService] ${alunoIdsValidos.length} alunos vlidos encontrados`
      );

      // Primeiro, remover todos os alunos atuais da aula
      const { error: deleteError } = await supabase
        .from("aula_alunos")
        .delete()
        .eq("aula_id", aulaId);

      if (deleteError) {
        console.error(
          `[AulaAlunosService] Erro ao remover alunos existentes:`,
          deleteError
        );
        throw deleteError;
      }

      // Se no temos novos alunos para adicionar, retorna
      if (alunoIdsValidos.length === 0) {
        console.log(
          `[AulaAlunosService] Nenhum aluno para adicionar  aula ${aulaId}`
        );
        return true;
      }

      // Adicionar os novos alunos
      const novasRelacoes = alunoIdsValidos.map((alunoId) => ({
        aula_id: aulaId,
        aluno_id: alunoId,
      }));

      console.log(
        `[AulaAlunosService] Inserindo ${novasRelacoes.length} alunos na aula ${aulaId}`
      );

      const { data, error: insertError } = await supabase
        .from("aula_alunos")
        .insert(novasRelacoes)
        .select();

      if (insertError) {
        console.error(
          `[AulaAlunosService] Erro ao inserir novos alunos:`,
          insertError
        );
        throw insertError;
      }

      console.log(
        `[AulaAlunosService] Alunos atualizados com sucesso na aula ${aulaId}`
      );
      return true;
    } catch (err) {
      console.error(
        `[AulaAlunosService] Erro crtico ao atualizar alunos:`,
        err
      );
      throw err;
    }
  },

  /**
   * Atualiza o nmero total de alunos em uma aula
   * @param {string} aulaId - ID da aula
   * @returns {Promise<number>} - Nmero total de alunos atualizado
   */
  async atualizarTotalAlunos(aulaId) {
    // Obter contagem de alunos
    const { count, error: countError } = await supabase
      .from("aula_alunos")
      .select("*", { count: "exact", head: true })
      .eq("aula_id", aulaId);

    if (countError) throw countError;

    console.log(
      `Atualizando contador de alunos para aula ${aulaId}: ${count} alunos`
    );

    // Atualizar o campo total_alunos na aula
    const { data, error: updateError } = await supabase
      .from("aulas")
      .update({
        total_alunos: count,
        totalAlunos: count, // Atualizar ambos os campos para garantir compatibilidade
      })
      .eq("id", aulaId)
      .select()
      .single();

    if (updateError) throw updateError;

    console.log(
      `Contador atualizado com sucesso: ${data.total_alunos} alunos para aula ${aulaId}`
    );

    return data.total_alunos;
  },

  /**
   * Atualiza as observaes de um aluno em uma aula especfica
   * @param {string} aulaId - ID da aula
   * @param {string} alunoId - ID do aluno
   * @param {string} observacoes - Texto das observaes
   * @returns {Promise<Object>} - Os dados atualizados
   */
  async atualizarObservacoes(aulaId, alunoId, observacoes) {
    console.log(
      `[AulaAlunosService] Atualizando observaes do aluno ${alunoId} na aula ${aulaId}`
    );

    try {
      // Verificar se os IDs so vlidos
      if (!aulaId || !alunoId) {
        console.error("[AulaAlunosService] IDs invlidos:", {
          aulaId,
          alunoId,
        });
        return {
          error: true,
          message: "IDs de aula ou aluno invlidos",
        };
      }

      // Atualizar as observaes na tabela de relacionamento
      const { data, error } = await supabase
        .from("aula_alunos")
        .update({ observacoes: observacoes })
        .eq("aula_id", aulaId)
        .eq("aluno_id", alunoId)
        .select()
        .single();

      if (error) {
        console.error(
          `[AulaAlunosService] Erro ao atualizar observaes:`,
          error
        );
        return {
          error: true,
          message: error.message || "Erro ao atualizar observaes",
          details: error,
        };
      }

      console.log(
        `[AulaAlunosService] Observaes atualizadas com sucesso para o aluno ${alunoId} na aula ${aulaId}`
      );
      return data;
    } catch (err) {
      console.error(`[AulaAlunosService] Erro crtico:`, err);
      return {
        error: true,
        message: err.message || "Erro inesperado ao atualizar observaes",
        details: err,
      };
    }
  },
};

export default aulaAlunosService;
