import { supabase } from "./supabase";

/**
 * Funo para criar um novo usurio professor com o email prof@ironhouse.com
 */
export async function criarProfessorCorreto() {
  try {
    console.log(
      "Tentando criar um novo usurio professor com email correto..."
    );

    // Definir email e senha que correspondem aos usados na tentativa de login
    const email = "prof@ironhouse.com";
    const senha = "Professor@2025"; // Voc deve alterar esta senha aps o login

    // Criar usurio professor no Supabase
    // eslint-disable-next-line no-unused-vars
    const { data, error } = await supabase.auth.signUp({
      email: email,
      password: senha,
      options: {
        data: {
          nome: "Professor IronHouse",
          role: "professor",
        },
      },
    });

    if (error) {
      console.error("Erro ao criar usurio professor:", error);
      return { success: false, error: error.message };
    }

    console.log("Novo usurio professor criado com sucesso!");
    console.log("Email:", email);
    console.log("Senha:", senha);
    console.log(
      "IMPORTANTE: Faa login com estas credenciais e altere a senha imediatamente."
    );

    return {
      success: true,
      data: {
        email: email,
        senha: senha,
      },
    };
  } catch (error) {
    console.error("Erro inesperado ao criar professor:", error);
    return { success: false, error: error.message };
  }
}

/**
 * Funo para verificar se o usurio professor existe
 */
export async function verificarProfessor() {
  try {
    console.log("Verificando se o usurio professor existe...");

    // Usar admin para verificar usurios no  recomendado em produo
    // Este  apenas um exemplo de verificao simplificada
    const { data, error } = await supabase.auth.signInWithPassword({
      email: "prof@ironhouse.com",
      password: "Professor@2025",
    });

    if (error) {
      console.log("Usurio professor no encontrado ou credenciais invlidas");
      return { success: false, error: error.message };
    }

    console.log("Usurio professor encontrado!");
    return { success: true, data };
  } catch (error) {
    console.error("Erro ao verificar professor:", error);
    return { success: false, error: error.message };
  }
}
