import { supabase } from "./supabase";

// Funo auxiliar para emitir evento de erro de cache
const emitSchemaCacheError = (error) => {
  if (error && error.message && error.message.includes("schema cache")) {
    // Disparar evento personalizado para notificar App.js
    const event = new CustomEvent("schema-cache-error", {
      detail: { message: error.message },
    });
    window.dispatchEvent(event);
    console.warn(
      "Erro de cache do esquema detectado. Evento disparado para possvel recarga."
    );
  }
};

// Funes individuais
async function getAll() {
  try {
    const { data, error } = await supabase
      .from("alunos")
      .select("*")
      .order("nome");

    if (error) throw error;
    return data;
  } catch (error) {
    console.error("Erro ao buscar alunos:", error);
    emitSchemaCacheError(error);
    throw error;
  }
}

async function createAluno(alunoData) {
  try {
    // Remover a lgica de normalizao para 'lesao'
    // Confiar que o frontend envia 'Nao', 'Sim - Lesao Grave' ou 'Sim - Lesao Moderada'

    // Mapear tipoLesao (camelCase do frontend) para tipo_lesao (snake_case do banco)
    const dadosParaSupabase = {
      ...alunoData, // Usar os dados diretamente como vieram do frontend
      tipo_lesao: alunoData.tipoLesao || null, // Mapeamento explcito
    };

    // Remover a chave original tipoLesao se existir para evitar conflito
    delete dadosParaSupabase.tipoLesao;

    console.log("Dados finais sendo enviados para o banco:", dadosParaSupabase);

    const { data, error } = await supabase
      .from("alunos")
      .insert([dadosParaSupabase]) // Usar os dados mapeados
      .select();

    if (error) throw error;
    // Retornar os dados como vieram do Supabase
    return data[0];
  } catch (error) {
    console.error("Erro ao criar aluno:", error);
    emitSchemaCacheError(error);
    throw error;
  }
}

async function updateAluno(id, alunoData) {
  try {
    // Garantir que o id seja vlido
    if (!id) {
      throw new Error("ID do aluno no fornecido");
    }

    // Log detalhado para debug
    console.log(
      `[updateAluno] Iniciando atualizao do aluno ${id} com dados:`,
      alunoData
    );

    // Tratar caso especial de atualizao de status
    if (alunoData.status !== undefined) {
      console.log(
        `[updateAluno] Atualizando status do aluno para: ${alunoData.status}`
      );

      // Garantir que o status seja uma string vlida
      if (typeof alunoData.status !== "string") {
        alunoData.status = alunoData.status ? "ativo" : "inativo";
      }
    }

    // Executar a atualizao no Supabase
    const { data, error } = await supabase
      .from("alunos")
      .update(alunoData)
      .eq("id", id)
      .select();

    if (error) {
      console.error("[updateAluno] Erro do Supabase:", error);
      // Verificar se  um erro de cache de esquema
      emitSchemaCacheError(error);
      throw error;
    }

    if (!data || data.length === 0) {
      console.warn(
        `[updateAluno] Nenhum dado retornado ao atualizar aluno ${id}`
      );

      // Tente buscar o aluno para confirmar se a atualizao foi bem-sucedida
      const { data: alunoAtualizado, error: erroConsulta } = await supabase
        .from("alunos")
        .select("*")
        .eq("id", id)
        .single();

      if (erroConsulta) {
        console.error(
          "[updateAluno] Erro ao verificar atualizao:",
          erroConsulta
        );
        throw new Error("Erro ao verificar a atualizao do aluno");
      }

      if (alunoAtualizado) {
        console.log(
          "[updateAluno] Aluno encontrado aps atualizao:",
          alunoAtualizado
        );
        return alunoAtualizado;
      }

      return null;
    }

    console.log("[updateAluno] Aluno atualizado com sucesso:", data[0]);
    return data[0];
  } catch (error) {
    console.error("[updateAluno] Erro ao atualizar aluno:", error);
    emitSchemaCacheError(error);
    throw error;
  }
}

async function deleteAluno(id) {
  try {
    const { error } = await supabase.from("alunos").delete().eq("id", id);

    if (error) {
      emitSchemaCacheError(error);
      throw error;
    }
  } catch (error) {
    console.error("Erro ao deletar aluno:", error);
    emitSchemaCacheError(error);
    throw error;
  }
}

// Nova funo para buscar a ltima aula realizada por um aluno
async function getUltimaAulaRealizada(alunoId) {
  try {
    // 1. Encontrar os IDs das aulas que o aluno participou
    const { data: aulaAlunosData, error: aulaAlunosError } = await supabase
      .from("aula_alunos")
      .select("aula_id")
      .eq("aluno_id", alunoId);

    if (aulaAlunosError) throw aulaAlunosError;
    if (!aulaAlunosData || aulaAlunosData.length === 0) {
      return null; // Aluno no participou de nenhuma aula
    }

    const aulaIds = aulaAlunosData.map((item) => item.aula_id);

    // 2. Buscar a aula mais recente com status 'realizada' entre essas aulas
    const { data: ultimaAulaData, error: ultimaAulaError } = await supabase
      .from("aulas")
      .select("data, status")
      .in("id", aulaIds)
      .eq("status", "realizada")
      .order("data", { ascending: false })
      .limit(1)
      .maybeSingle(); // Use maybeSingle para retornar null se no encontrar

    if (ultimaAulaError) throw ultimaAulaError;

    return ultimaAulaData; // Retorna a ltima aula realizada ou null
  } catch (error) {
    console.error("Erro ao buscar ltima aula realizada do aluno:", error);
    // No emitir erro de cache aqui, pois pode ser um erro esperado (ex: aluno novo)
    // emitSchemaCacheError(error);
    throw error; // Propagar o erro para ser tratado no componente
  }
}

