import { supabase } from "./supabase";

const exerciciosService = {
  // Criar um novo exerccio
  async create(exercicioData) {
    try {
      const { data, error } = await supabase
        .from("exercicios")
        .insert({
          nome: exercicioData.nome,
          musculatura: exercicioData.musculatura,
          aparelho: exercicioData.aparelho, // Adicionado aparelho
          created_at: new Date().toISOString(),
        })
        .select()
        .single();

      if (error) throw error;

      // Mapear para o formato usado na aplicao
      return {
        id: data.id,
        _id: data.id, // Para compatibilidade
        nome: data.nome,
        musculatura: data.musculatura,
        aparelho: data.aparelho, // Adicionado aparelho
      };
    } catch (error) {
      console.error("Erro ao criar exerccio:", error);
      throw error;
    }
  },

  // Buscar todos os exerccios
  async getAll() {
    try {
      const { data, error } = await supabase
        .from("exercicios")
        .select("*")
        .order("nome");

      if (error) throw error;

      // Mapear para o formato usado na aplicao
      return data.map((exercicio) => ({
        id: exercicio.id,
        _id: exercicio.id, // Para compatibilidade
        nome: exercicio.nome,
        musculatura: exercicio.musculatura,
        aparelho: exercicio.aparelho, // Adicionado aparelho
      }));
    } catch (error) {
      console.error("Erro ao buscar exerccios:", error);
      throw error;
    }
  },

  // Buscar exerccio por ID
  async getById(id) {
    try {
      const { data, error } = await supabase
        .from("exercicios")
        .select("*")
        .eq("id", id)
        .single();

      if (error) throw error;

      return {
        id: data.id,
        _id: data.id, // Para compatibilidade
        nome: data.nome,
        musculatura: data.musculatura,
        aparelho: data.aparelho, // Adicionado aparelho
      };
    } catch (error) {
      console.error(`Erro ao buscar exerccio com id ${id}:`, error);
      throw error;
    }
  },

  // Atualizar exerccio por ID
  async update(id, exercicioData) {
    try {
      const { data, error } = await supabase
        .from("exercicios")
        .update({
          nome: exercicioData.nome,
          musculatura: exercicioData.musculatura,
          aparelho: exercicioData.aparelho, // Adicionado aparelho
          updated_at: new Date().toISOString(),
        })
        .eq("id", id)
        .select()
        .single();

      if (error) throw error;

      return {
        id: data.id,
        _id: data.id, // Para compatibilidade
        nome: data.nome,
        musculatura: data.musculatura,
        aparelho: data.aparelho, // Adicionado aparelho
      };
    } catch (error) {
      console.error(`Erro ao atualizar exerccio com id ${id}:`, error);
      throw error;
    }
  },

  // Excluir exerccio por ID
  async delete(id) {
    try {
      const { error } = await supabase.from("exercicios").delete().eq("id", id);

      if (error) throw error;

      return { success: true };
    } catch (error) {
      console.error(`Erro ao excluir exerccio com id ${id}:`, error);
      throw error; // Re-throw para que o componente possa tratar
    }
  },

  // Associar exerccios a uma aula
  async associarExerciciosAula(aulaId, exerciciosIds) {
    // Esta funo parece ser um placeholder, no h cdigo assncrono real aqui.
    // Removendo try/catch desnecessrio ou adicionando lgica real se necessrio.
    // Por enquanto, apenas retornando sucesso como antes, mas sem o try/catch.
    console.log(
      `Associando exerccios ${exerciciosIds}  aula ${aulaId} (placeholder)`
    );
    // Para implementar quando a tabela de relacionamento entre aulas e exerccios for criada
    // Por enquanto, salvamos essa relao apenas no frontend
    return { success: true };
    /*
    // Exemplo se houvesse lgica assncrona:
    try {
      // const { data, error } = await supabase...;
      // if (error) throw error;
      return { success: true };
    } catch (error) {
      console.error(`Erro ao associar exerccios  aula ${aulaId}:`, error);
      throw error; // Re-throw para que o componente possa tratar
    }
    */
  },

  // Adicionando a funo getAllMusculaturas que estava faltando
  // Esta funo busca todas as musculaturas distintas da tabela de exerccios
  async getAllMusculaturas() {
    try {
      // Usando uma view ou funo RPC se disponvel seria mais eficiente.
      // Como alternativa, buscamos todos os exerccios e extramos as musculaturas.
      const { data, error } = await supabase
        .from("exercicios")
        .select("musculatura")
        .neq("musculatura", null) // Ignora valores nulos
        .neq("musculatura", ""); // Ignora strings vazias

      if (error) throw error;

      // Extrai musculaturas nicas
      const musculaturasUnicas = [
        ...new Set(data.map((item) => item.musculatura)),
      ];

      // Mapeia para o formato esperado (array de strings ou objetos)
      // Retornando como array de strings por simplicidade, ajuste se necessrio
      return musculaturasUnicas;
    } catch (error) {
      console.error("Erro ao buscar musculaturas:", error);
      throw error;
    }
  },

  // Buscar exerccios associados a uma aula
  async getExerciciosByAulaId(aulaId) {
    try {
      // Primeiro buscar os IDs de exerccios relacionados a esta aula
      const { data: aulaExercicios, error: relError } = await supabase
        .from("aula_exercicios")
        .select("exercicio_id")
        .eq("aula_id", aulaId);

      if (relError) {
        console.error(
          `Erro ao buscar relao aula-exerccios: ${relError.message}`
        );
        return [];
      }

      // Se no encontrou relaes, retornar array vazio
      if (!aulaExercicios || aulaExercicios.length === 0) {
        return [];
      }

      // Extrair os IDs dos exerccios
      const exerciciosIds = aulaExercicios.map((item) => item.exercicio_id);

      // Buscar os detalhes completos dos exerccios
      const { data: exercicios, error: exerciciosError } = await supabase
        .from("exercicios")
        .select("*")
        .in("id", exerciciosIds);

      if (exerciciosError) {
        console.error(
          `Erro ao buscar detalhes dos exerccios: ${exerciciosError.message}`
        );
        return [];
      }

      return exercicios || [];
    } catch (error) {
      console.error(`Erro ao buscar exerccios da aula ${aulaId}:`, error);
      return [];
    }
  },
};

export default exerciciosService;
