import { supabase } from "./supabase";

// Sistema de autenticao com Supabase
const authService = {
  // Registro de usurio
  async register(email, password, userData) {
    try {
      // Validar o e-mail antes de prosseguir
      if (!this.validateEmail(email)) {
        return { success: false, error: "Formato de email invlido" };
      }

      // Registrar o usurio no Supabase Auth
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email,
        password,
        options: {
          data: {
            nome: userData.nome,
            role: userData.role || "professor",
          },
          // No redirecionar automaticamente
          emailRedirectTo: window.location.origin,
        },
      });

      if (authError) {
        console.error("Erro durante registro:", authError);
        throw new Error(authError.message);
      }

      return { success: true, data: authData };
    } catch (error) {
      console.error("Exceo durante registro:", error);
      return { success: false, error: error.message };
    }
  },

  // Login de usurio
  async login(email, password) {
    try {
      console.log("Tentando login para:", email);
      console.log("Domnio atual:", window.location.hostname);

      // Usar Supabase para autenticao
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password,
      });

      if (error) {
        console.error("Erro de autenticao:", error);
        console.error("Cdigo do erro:", error.status, error.name);
        throw new Error(error.message);
      }

      if (!data || !data.session) {
        console.error("Sesso no foi criada aps login");
        throw new Error("Falha ao criar sesso de usurio");
      }

      // Verificar se o usurio tem role definido nos metadados
      const userRole = data.user?.user_metadata?.role;
      if (!userRole) {
        console.warn("Usurio no tem role definido nos metadados");
      }

      console.log("Login bem-sucedido:", data.user.email);
      console.log("Detalhes da sesso:", {
        expiry: data.session.expires_at,
        tokenType: data.session.token_type,
        userID: data.user.id,
      });

      return {
        success: true,
        data: {
          user: {
            id: data.user.id,
            email: data.user.email,
            role: userRole || "professor", // Fallback para professor se no estiver definido
            nome: data.user.user_metadata?.nome || "Usurio",
          },
          session: data.session,
        },
      };
    } catch (error) {
      console.error("Exceo durante login:", error);
      return {
        success: false,
        error: error.message,
        details: "Verifique suas credenciais e tente novamente",
      };
    }
  },

  // Valida formato de email
  validateEmail(email) {
    if (!email) return false;
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return re.test(String(email).toLowerCase());
  },

  // Logout de usurio
  async logout() {
    try {
      console.log("Iniciando logout...");
      const { error } = await supabase.auth.signOut();

      if (error) {
        console.error("Erro durante logout:", error);
        throw new Error(error.message);
      }

      console.log("Logout realizado com sucesso");
      return { success: true };
    } catch (error) {
      console.error("Exceo durante logout:", error);
      return { success: false, error: error.message };
    }
  },

  // Obter usurio atual
  async getCurrentUser() {
    try {
      console.log("Verificando usurio atual...");

      // Primeiro tenta obter a sesso (mais rpido)
      const { data: sessionData } = await supabase.auth.getSession();

      if (!sessionData?.session) {
        console.log("Nenhuma sesso ativa encontrada");
        return { success: false, error: "Sem sesso ativa" };
      }

      // Se temos uma sesso, obtm os dados do usurio completos
      const { data, error } = await supabase.auth.getUser();

      if (error) {
        console.error("Erro ao obter usurio:", error);
        throw new Error(error.message);
      }

      if (!data.user) {
        console.log("Usurio no encontrado mesmo com sesso ativa");
        return { success: false, error: "Usurio no encontrado" };
      }

      console.log("Usurio atual recuperado com sucesso:", data.user.email);

      return {
        success: true,
        data: {
          id: data.user.id,
          email: data.user.email,
          nome: data.user.user_metadata?.nome || "Usurio",
          role: data.user.user_metadata?.role || "professor",
        },
      };
    } catch (error) {
      console.error("Exceo ao obter usurio atual:", error);
      return { success: false, error: error.message };
    }
  },

  // Verificar se a sesso  vlida
  async verificarSessao() {
    try {
      const { data, error } = await supabase.auth.getSession();

      if (error) {
        console.error("Erro ao verificar sesso:", error);
        return { valida: false, error: error.message };
      }

      const sessaoValida = !!data?.session;
      console.log("Sesso vlida?", sessaoValida);

      return { valida: sessaoValida };
    } catch (error) {
      console.error("Exceo ao verificar sesso:", error);
      return { valida: false, error: error.message };
    }
  },

  // Criar usurio admin (usado na inicializao)
  async criarUsuarioAdmin() {
    try {
      // Verificar se j existe um admin
      const { data, error } = await supabase
        .from("profiles")
        .select("*")
        .eq("role", "admin")
        .limit(1);

      if (error) {
        console.error("Erro ao verificar admin existente:", error.message);
        throw new Error(error.message);
      }

      if (data && data.length > 0) {
        return { success: true };
      }

      console.log("Criando usurio admin");
      // Se no existe, criar o usurio admin
      return await this.register("admin@example.com", "admin123", {
        nome: "Administrador",
        role: "admin",
      });
    } catch (error) {
      console.error("Exceo ao criar admin:", error.message);
      return { success: false, error: error.message };
    }
  },

  // Criar usurio professor (usado na inicializao)
  async criarUsuarioProfessor() {
    try {
      // Verificar se j existe um professor com este email
      const { data, error } = await supabase
        .from("profiles")
        .select("*")
        .eq("email", "professor@example.com")
        .limit(1);

      if (error) {
        console.error("Erro ao verificar professor existente:", error.message);
        throw new Error(error.message);
      }

      if (data && data.length > 0) {
        return { success: true };
      }

      console.log("Criando usurio professor");
      // Se no existe, criar o usurio professor
      return await this.register("professor@example.com", "professor123", {
        nome: "Professor",
        role: "professor",
      });
    } catch (error) {
      console.error("Exceo ao criar professor:", error.message);
      return { success: false, error: error.message };
    }
  },
};

export default authService;
