import React, { useState } from "react";
import "../styles/Cadastros.css";
import {
  criarProfessorCorreto,
  verificarProfessor,
} from "../services/resetProfessor";

export default function Configuracoes() {
  const [message, setMessage] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const handleResetProfessor = async () => {
    try {
      setIsLoading(true);
      setMessage("Verificando se o professor j existe...");

      // Primeiro verificar se o professor j existe
      const verificacao = await verificarProfessor();

      if (verificacao.success) {
        setMessage(
          "O usurio professor j existe! Voc pode fazer login com prof@ironhouse.com e a senha padro."
        );
        return;
      }

      // Se no existir, criar um novo
      setMessage("Criando novo usurio professor...");
      const resultado = await criarProfessorCorreto();

      if (resultado.success) {
        setMessage(
          `Usurio professor criado com sucesso!\n` +
            `Email: ${resultado.data.email}\n` +
            `Senha: ${resultado.data.senha}\n` +
            `IMPORTANTE: Faa login com estas credenciais e altere a senha aps o primeiro acesso.`
        );
      } else {
        setMessage(`Erro ao criar usurio: ${resultado.error}`);
      }
    } catch (error) {
      setMessage(`Erro inesperado: ${error.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="container-cadastro">
      <h2>Configuraes de Usurios</h2>

      {message && (
        <div className={isLoading ? "message loading" : "message"}>
          {message.split("\n").map((line, i) => (
            <p key={i}>{line}</p>
          ))}
        </div>
      )}

      <div className="actions">
        <button
          onClick={handleResetProfessor}
          className="btn-resetar"
          disabled={isLoading}
        >
          {isLoading ? "Processando..." : "Resetar Usurio Professor"}
        </button>
      </div>

      <h3>Gerenciamento de Usurios</h3>
      <p className="info-text">
        Os usurios agora so gerenciados diretamente atravs do Supabase:
      </p>
      <ol className="info-list">
        <li>Acesse o dashboard do Supabase do projeto</li>
        <li>Navegue at a seo "Authentication" para criar novos usurios</li>
        <li>
          Aps criar um usurio, configure seu perfil na tabela "profiles"
        </li>
        <li>
          Configure a funo (role) como "admin" ou "professor" conforme
          necessrio
        </li>
      </ol>

      <p className="info-text">
        <strong>Nota:</strong> Usurios criados no antigo sistema local no
        funcionaro com o novo sistema de autenticao.
      </p>
    </div>
  );
}
